import React from 'react'
import {useLocation} from 'react-router-dom'
import { useParams } from 'react-router'
import { useSelector, useDispatch } from 'react-redux'
import { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { getInfos } from '../../redux/pokemons/infosReducer'
import { unsetInfos } from '../../redux/pokemons/infosReducer'
import {v4 as uuidv4} from 'uuid'
import './Evolution.css'

export default function Evolution() {

    const location = useLocation();

    const params = useParams();

    const {infos} = useSelector(state => ({
        ...state.infosReducer
    }))

    const dispatch = useDispatch()
    
    useEffect(() => {
        if (infos.length === 0) {
            dispatch(getInfos(params.slug))
        }
    }, [])

    return (
        <>
            {infos.map(item => {
                return(
                    <div key={uuidv4()} className="infos-pokemon">
                        <h1>{item.name}</h1>
                        <img className="pokemon-img" src={item.sprites.other["official-artwork"].front_default} alt={item.name} />
                        <div className="pokemon-size">
                            <div className="pokemon-types">
                                <p>Type(s) :</p>
                                <ul>
                                    {item.types.map(item => {
                                        return(
                                            <li key={uuidv4()} className="pokemon-type">{item.type.name}</li> 
                                            )
                                        })}
                                    </ul>
                            </div>
                            <p>
                                Weight : {item.weight/10} kg
                                <br/>
                                Height : {item.height/10} m
                            </p>
                        </div>
                        <div className="pokemon-evolution">
                            {<Link 
                            onClick={() => {
                                dispatch(unsetInfos())
                            }}
                            to = {"/-Pokedex-en-REACT/Infos/" + location.state}
                            state = {location.state}
                            >
                            Return to previous state : {location.state}
                            </Link>}
                        </div>
                    </div>
                )
            })}
                
        </>
    )
}
