import React from 'react'
import './Infos.css'
import { useParams } from 'react-router'
import { useSelector, useDispatch } from 'react-redux'
import { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { getInfos } from '../../redux/pokemons/infosReducer'
import { unsetInfos } from '../../redux/pokemons/infosReducer'
import { getEvolution } from '../../redux/pokemons/evolutionReducer'
import { unsetEvolution } from '../../redux/pokemons/evolutionReducer'
import {v4 as uuidv4} from 'uuid'

export default function Infos() {

    
    const params = useParams();
    
    const {evolutions,infos} = useSelector(state => ({
        ...state.evolutionReducer,
        ...state.infosReducer
    }))    
    
    const dispatch = useDispatch()
    
    useEffect(() => {
        if (infos.length === 0) {
            dispatch(getInfos(params.slug))
        }
    }, [])
    
    useEffect(() => {
        if (infos[0]) {   
            dispatch(getEvolution(infos[0].species.url));
        }
    }, [infos])
    
    return (
        <>
            {infos.map(item => {
                return(
                    <div className="infos-pokemon" key={uuidv4()}>
                        <h1>{item.name}</h1>
                        <img className="pokemon-img" src={item.sprites.other["official-artwork"].front_default} alt={item.name} />
                        <div className="pokemon-size">
                            <div className="pokemon-types">
                                <p>Type(s) :</p>
                                <ul>
                                    {item.types.map(item => {
                                        return(
                                            <li key={uuidv4()} className="pokemon-type">{item.type.name}</li> 
                                            )
                                        })}
                                </ul>
                            </div>
                            <p>
                                Weight : {item.weight/10} kg
                                <br/>
                                Height : {item.height/10} m
                            </p>
                        </div>
                        <div className="pokemon-evolution">
                            {evolutions.map(item => {
                                    if (item !== params.slug) {
                                        return(
                                        <Link 
                                            onClick={() => {
                                                dispatch(unsetInfos())
                                                dispatch(unsetEvolution())
                                            }}
                                            key={uuidv4()} 
                                            to={"/-Pokedex-en-REACT/Evolution/" + item}
                                            state={params.slug}
                                        >
                                            Evolution  : {item}
                                        </Link>
                                        )
                                    } else return "";
                                })}
                        </div>
                    </div>
                )
            })}   
        </>
    )
}
